@import 'styles/variables';
@import 'styles/svg';
@import 'styles/form_fields';
@import 'styles/icons';
@import 'styles/tooltip';

/**
 * Families shared folder dialog
 */

#familiesSharedFolderDialog {
  height: 80%;
  max-width: 531px;
}

#groupName {
  margin-top: 7px;
}

.wbtn {
  float:left;
}

#notifyFamilyMembersText {
  padding-left: 3px;
}

.tooltipFamily:hover + .tooltiptext {
  display:inline;
  top:80px;
  margin-left:10px;
  left:auto;
  max-width:200px;
}

.familiesSharedFolder {
  font-family: 'Open Sans', sans-serif;
  .shared-folder-name {
    width: 153px;
    height: 22px;
    font-size: 16px;
    font-weight: 600;
    color: #000000;
  }
  input[type='text'] {
    font-size: 14px;
  }
  select {
    font-size: 14px;
  }
  table {
    margin-top: 20px;
    border-collapse: collapse;
    font-size: 13px;
    tr {
      background-color: #ffffff;
      &:nth-child(even) {
        background-color: #fbfbfb;
      }
      th, td {
        border: solid 1px #d8d8d8;
        text-align: left;
        padding: 0px 20px;
        height: 51px;
        max-width: 200px;
        overflow: hidden;
      }
      th {
        background-color: #515151;
        font-weight: 500;
        color: #ffffff;
      }
    }
  }
  .emailFamilyMembers {
    padding-top: 10px;
    font-size: 13px;

    /* Minor modifications from the original _form_fields.scss checkbox */
    input[type='checkbox'] {
      &:after {
        top: -1px;
        background: url($svgBetterCheckboxEmpty);
      }
      &:checked:after {
        background: url($svgBetterCheckboxChecked);
      }
    }
  }
}
