.tooltiptext {
  display: none;
  color: white;
  background-color: #646D73;
  width: 150px;
  font-family: ArialMT;
  font-size: 12px;
  letter-spacing: .3px;
  border-radius: 4px;
  padding: 6px 4px;
  line-height: 1.33;
  box-shadow: none;
  font-weight: 600;
  position: absolute;
  z-index: 1;
  text-align:justify;
  &:after {
    right: 100%;
    top: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-right-color: #646D73;
    border-width: 5px;
    margin-top: -5px;
  }
}
